import { Fragment, useEffect, useState } from "react";
import "./EditProduct.css";
import { useAlert } from "react-alert";
import {
  MdAccountTree,
  MdDescription,
  MdSpellcheck,
  MdStorage,
} from "react-icons/md";
import { BiRupee } from "react-icons/bi";

import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import {
  clearErrors,
  getProductDetail,
  updateProduct,
} from "../../../../redux/actions/productAction";
import { UPDATE_PRODUCT_RESET } from "../../../../redux/constants/productConstant";
import { adminAuth, userAuth } from "../../../../util/userAuth";
import Button from "../../../Button/Button";
import MetaData from "../../../layout/MetaData/MetaData";
import SideBar from "../Dashboard/SideBar/SideBar";
import Loader from "../../../layout/Loader/Loader";

const EditProduct = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const alert = useAlert();
  const { id } = useParams();

  const { product } = useSelector((state) => state.productDetail);

  const {
    user,
    isAuthenticated,
    loading: userLoading,
  } = useSelector((state) => state.user);

  const { isUpdated, error, loading } = useSelector((state) => state.product);

  const categories = [
    "Laptop",
    "Footwear",
    "Bottom",
    "Top",
    "Attire",
    "Camera",
    "SmartPhone",
  ];
  const [newProduct, setNewProduct] = useState({
    name: "",
    price: 0,
    description: "",
    category: "",
    stock: 0,
  });

  const [images, setImages] = useState([]);
  const [oldImages, setOldImages] = useState([]);

  const [imagesPreview, setImagesPreview] = useState([]);

  const updateDataChange = (e) => {
    if (e.target.name === "images") {
      const files = Array.from(e.target.files);

      setImages([]);
      setImagesPreview([]);
      setOldImages([]);

      files.forEach((file) => {
        const reader = new FileReader();

        reader.onload = () => {
          if (reader.readyState === 2) {
            setImagesPreview((old) => [...old, reader.result]);
            setImages((old) => [...old, file]);
          }
        };

        reader.readAsDataURL(file);
      });
    } else {
      setNewProduct({ ...newProduct, [e.target.name]: e.target.value });
    }
  };

  const updateProductHandler = (e) => {
    e.preventDefault();

    const myForm = new FormData();
    myForm.set("name", newProduct.name);
    myForm.set("price", newProduct.price);
    myForm.set("description", newProduct.description);
    myForm.set("category", newProduct.category);
    myForm.set("stock", newProduct.stock);

    images.forEach((img) => {
      myForm.append("images", img);
    });

    dispatch(updateProduct(id, myForm));
  };

  useEffect(() => {
    if (product && product._id !== id) {
      dispatch(getProductDetail(id));
    } else {
      setNewProduct({
        name: product.name,
        price: product.price,
        description: product.description,
        category: product.category,
        stock: product.stock,
      });
      setOldImages(product.images);
    }

    userAuth(isAuthenticated, navigate);
    if (user) 
    adminAuth(true, user, navigate, alert);

    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    if (isUpdated) {
      alert.success("Product Updated Successfully");
      dispatch(getProductDetail(id));
      dispatch({ type: UPDATE_PRODUCT_RESET });
      navigate("/admin/products");
    }
  }, [
    isAuthenticated,
    navigate,
    user,
    alert,
    dispatch,
    error,
    isUpdated,
    product,
    id,
  ]);

  return userLoading ? (
    <Loader />
  ) : (
    <Fragment>
      <MetaData title={`Create Product`} />

      <div className="dashboard">
        <SideBar />

        <div className="newProductContainer">
          <form
            className="shippingForm"
            encType="multipart/form-data"
            onSubmit={updateProductHandler}
          >
            <h1>Edit Product</h1>
            <div>
              <MdSpellcheck />
              <input
                type="text"
                placeholder="Product Name"
                required
                name="name"
                value={newProduct.name}
                onChange={updateDataChange}
              />
            </div>

            <div>
              <BiRupee />
              <input
                type="text"
                name="price"
                placeholder="Price"
                required
                value={newProduct.price}
                onChange={updateDataChange}
              />
            </div>

            <div>
              <MdDescription />
              <textarea
                type="number"
                name="description"
                placeholder="Product Description"
                required
                value={newProduct.description}
                cols="30"
                rows="1"
                onChange={updateDataChange}
              ></textarea>
            </div>

            <div>
              <MdAccountTree />
              <select
                required
                value={newProduct.category}
                name="category"
                onChange={updateDataChange}
              >
                <option value="">Select Category</option>
                {categories.map((cate, index) => (
                  <option key={index} value={cate}>
                    {cate}
                  </option>
                ))}
              </select>
            </div>

            <div>
              <MdStorage />
              <input
                type="number"
                name="stock"
                placeholder="Stock"
                required
                value={newProduct.stock}
                onChange={updateDataChange}
              />
            </div>
            <div id="createNewProductImage">
              <input
                type="file"
                multiple
                name="images"
                accept="image/*"
                onChange={updateDataChange}
              />
            </div>
            <div>
              <div id="createNewProductImagePreview">
                {oldImages &&
                  oldImages.map((image, index) => (
                    <img key={index} src={`/${image}`} alt={`${index} img`} />
                  ))}
                {imagesPreview.map((image, index) => (
                  <img key={index} src={image} alt={`${index} img`} />
                ))}
              </div>
            </div>
            <div>
              <Button
                id="createProductBtn"
                type="submit"
                disabled={loading ? true : false}
              >
                Update
              </Button>
            </div>
          </form>
        </div>
      </div>
    </Fragment>
  );
};

export default EditProduct;
